html {
    margin: 0;
    width: 100%;
    height: auto;
    min-width: 100%;
    min-height: 100%;
    position: relative;  
/*     color: white; */
    padding-bottom: 50px;
    overflow-x: hidden;
    
}

html::before {
    background-image: url('/field.jpg');
    background-size: cover;
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    opacity: 0.8;

}

html::after {
    background-color: rgb(87, 87, 87);
    
    content: "";
  
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    z-index: -1;
    opacity: 0.3; 

}  

